<template>
	<view class="app-container">
		<!-- Ê†∏ÂøÉÂÜÖÂÆπÂå∫Âüü -->
		<view class="main-content">
			<!-- Âú∫ÊôØÈÄâÊã©ÈÉ®ÂàÜ -->
			<view class="scene-section">
				<!-- ÊµãËØïÊåâÈíÆ -->
				<view class="test-section">
					<button class="test-btn" @click="testSupabaseConnection" :loading="testing">
						{{ testing ? 'ÊµãËØï‰∏≠...' : 'üîç ÊµãËØïÊï∞ÊçÆÂ∫ìËøûÊé•' }}
					</button>
					<view v-if="testResult" class="test-result" :class="testResult.type">
						{{ testResult.message }}
					</view>
				</view>
				
			<!-- Ê¨¢ËøéÊèèËø∞ -->
			<view class="welcome-card">
				<text class="welcome-title">Ê¨¢ËøéÊù•Âà∞ÂìÑ‰∏ÄÂìÑ‰ªñÔºàÂ•πÔºâ</text>
				<text class="welcome-desc">
					Êú¨Á®ãÂ∫èÂü∫‰∫é AI ÊäÄÊúØÔºå‰Ω†ÈúÄË¶Å‰ΩøÁî®ËØ≠Ë®ÄÊäÄÂ∑ßÂíåÊ≤üÈÄöËÉΩÂäõÔºå
					Âú®ÈôêÂÆöÊ¨°Êï∞ÂÜÖËÆ©ÂØπÊñπÂéüË∞Ö‰Ω†ÔºåËøôÂπ∂‰∏çÂÆπÊòì„ÄÇ
				</text>
			</view>
			
			<text class="section-title">
				<text class="emoji">‚ú®</text> ÈÄâÊã©‰∏Ä‰∏™Âú∫ÊôØÔºåÁÑ∂ÂêéÂºÄÂßãÊ®°ÊãüÂìÑ‰Ω†ÁöÑËôöÊãüÁî∑/Â•≥ÊúãÂèãÂêß
			</text>
				
				<!-- ÊéíÂ∫èÈÄâÊã©‰∏ãÊãâÊ°Ü -->
				<picker @change="onSortChange" :value="sortIndex" :range="sortOptions" class="scene-picker">
					<view class="picker-text">{{sortOptions[sortIndex]}}</view>
				</picker>
				
				<!-- Âú∫ÊôØÂàÜÁ±ªÊ†áÁ≠æ -->
				<view class="category-tabs">
					<view 
						v-for="(category, index) in categories" 
						:key="index"
						class="category-tab"
						:class="{ active: activeCategory === category.name }"
						@click="onCategoryChange(category.name)"
					>
						{{category.name}} ({{category.count}})
					</view>
				</view>
				
				<!-- Âú∫ÊôØÂàóË°® -->
				<view class="scene-list">
					<view 
						v-for="(scene, index) in filteredScenes" 
						:key="index"
						class="scene-item"
						@click="onSceneSelect(scene)"
					>
						<text class="scene-title">{{scene.title}}</text>
						<text class="scene-stats">{{scene.times}}Ê¨°(ËÉúÁéá {{scene.winRate}}%)</text>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import { sceneService, gameRecordService } from '@/utils/supabase-helper.js'
	
	export default {
		data() {
			return {
				title: 'ÂìÑÂìÑÊ®°ÊãüÂô®',
				sortIndex: 0,
				sortOptions: ['ÈªòËÆ§', 'ÊúÄÊñ∞', 'ÊúÄÁÉ≠', 'ËÉúÁéáÊúÄÈ´ò', 'ËÉúÁéáÊúÄ‰Ωé'],
				categories: [],
				activeCategory: 'ÂÖ®ÈÉ®',
				scenes: [],
				testing: false,
				testResult: null,
				loadingScenes: false
			}
		},
		onLoad() {
			this.loadScenes()
			// ÂèØ‰ª•Ëá™Âä®ÊµãËØï‰∏ÄÊ¨°ÔºàÂèØÈÄâÔºâ
			// this.testSupabaseConnection()
		},
		onShow() {
			// ÊØèÊ¨°ËøîÂõûÈ¶ñÈ°µÊó∂Âà∑Êñ∞Âú∫ÊôØÁªüËÆ°Êï∞ÊçÆÔºàÊåëÊàòÊ¨°Êï∞„ÄÅËÉúÁéáÔºâ
			this.loadScenes()
		},
		computed: {
			// ËøáÊª§Âπ∂ÊéíÂ∫èÂú∫ÊôØÂàóË°®ÔºàÊîØÊåÅÂ§öÂàÜÁ±ªÔºâ
			filteredScenes() {
				let list = this.activeCategory === 'ÂÖ®ÈÉ®'
					? this.scenes
					: this.scenes.filter(scene => {
						// Â§ÑÁêÜÂ§öÂàÜÁ±ªÔºàÈÄóÂè∑ÂàÜÈöîÔºâ
						const categories = (scene.category || '').split(',').map(c => c.trim())
						return categories.includes(this.activeCategory)
					})
				
				const option = this.sortOptions[this.sortIndex]
				if (option === 'ÊúÄÊñ∞') {
					list = [...list].sort((a, b) => new Date(b.created_at || 0) - new Date(a.created_at || 0))
				} else if (option === 'ÊúÄÁÉ≠') {
					list = [...list].sort((a, b) => (b.play_count || 0) - (a.play_count || 0))
				} else if (option === 'ËÉúÁéáÊúÄÈ´ò') {
					list = [...list].sort((a, b) => (b.win_rate || 0) - (a.win_rate || 0))
				} else if (option === 'ËÉúÁéáÊúÄ‰Ωé') {
					list = [...list].sort((a, b) => (a.win_rate || 0) - (b.win_rate || 0))
				}
				return list
			}
		},
		methods: {
			// ‰ªéÊï∞ÊçÆÂ∫ìÂä†ËΩΩÂú∫ÊôØ
			async loadScenes() {
				this.loadingScenes = true
				try {
					const { data, error } = await sceneService.getAllScenes({
						status: 'active',
						limit: 100,
						orderBy: 'play_count',
						order: 'desc'
					})
					if (error) {
						console.error('Âä†ËΩΩÂú∫ÊôØÂ§±Ë¥•:', error)
						uni.showToast({
							title: 'Âä†ËΩΩÂú∫ÊôØÂ§±Ë¥•',
							icon: 'none'
						})
						return
					}

					// È¢ùÂ§ñËé∑ÂèñÂÖ®Â±ÄÁªüËÆ°Ôºà‰ªé game_records ËÆ°ÁÆóÔºåÁ°Æ‰øùÊúÄÊñ∞Ôºâ
					const { data: globalStats } = await gameRecordService.getGlobalSceneStats()

					this.scenes = (data || []).map(item => {
						const sid = item.id
						const override = globalStats && globalStats[sid] ? globalStats[sid] : null
						const playCount = override ? override.playCount : (item.play_count ?? 0)
						const winRateRaw = override ? override.winRate : (item.win_rate ?? 0)
						// ËÉúÁéáÊòæÁ§∫Âà∞Â∞èÊï∞ÁÇπÂêé‰∏Ä‰Ωç
						const winRateDisplay = Number(winRateRaw || 0).toFixed(1)
						return {
							id: item.id,
							title: item.title,
							category: item.category || 'ÂÖ∂‰ªñ',
							// ÂÖºÂÆπÂ±ïÁ§∫Â≠óÊÆµ
							times: playCount,
							winRate: winRateDisplay,
							play_count: playCount,
							win_rate: Number(winRateRaw) || 0,
							created_at: item.created_at
						}
					})
					// ÂàÜÁ±ªÊ†áÁ≠æÁªüËÆ°ÔºàÊîØÊåÅÂ§öÂàÜÁ±ªÔºåÁî®ÈÄóÂè∑ÂàÜÈöîÔºâ
					const categoryMap = {}
					this.scenes.forEach(s => {
						// Â§ÑÁêÜÂ§öÂàÜÁ±ªÔºàÈÄóÂè∑ÂàÜÈöîÔºâ
						const categories = (s.category || '').split(',').map(c => c.trim()).filter(c => c)
						if (categories.length === 0) {
							// Â¶ÇÊûúÊ≤°ÊúâÂàÜÁ±ªÔºåÂΩíÁ±ª‰∏∫"ÂÖ∂‰ªñ"
							categoryMap['ÂÖ∂‰ªñ'] = (categoryMap['ÂÖ∂‰ªñ'] || 0) + 1
						} else {
							// ÊØè‰∏™ÂàÜÁ±ªÈÉΩËÆ°Êï∞
							categories.forEach(cat => {
								categoryMap[cat] = (categoryMap[cat] || 0) + 1
							})
						}
					})
					this.categories = [{ name: 'ÂÖ®ÈÉ®', count: this.scenes.length }].concat(
						Object.entries(categoryMap).map(([name, count]) => ({ name, count }))
					)
					// ÈªòËÆ§ÈÄâ‰∏≠ÂÖ®ÈÉ®
					this.activeCategory = 'ÂÖ®ÈÉ®'
				} catch (err) {
					console.error('Âä†ËΩΩÂú∫ÊôØÂºÇÂ∏∏:', err)
					uni.showToast({
						title: 'Âä†ËΩΩÂºÇÂ∏∏',
						icon: 'none'
					})
				} finally {
					this.loadingScenes = false
				}
			},

			// ÊµãËØï Supabase Êï∞ÊçÆÂ∫ìËøûÊé•
			async testSupabaseConnection() {
				this.testing = true
				this.testResult = null
				
				try {
					console.log('ÂºÄÂßãÊµãËØï Supabase ËøûÊé•...')
					
					// ÊµãËØï1: Ëé∑ÂèñÂú∫ÊôØÂàóË°®
					const { data, error } = await sceneService.getAllScenes({
						status: 'active',
						limit: 100
					})
					
					if (error) {
						console.error('Êï∞ÊçÆÂ∫ìËøûÊé•Â§±Ë¥•:', error)
						this.testResult = {
							type: 'error',
							message: `‚ùå ËøûÊé•Â§±Ë¥•: ${error.message || error}`
						}
						uni.showToast({
							title: 'ËøûÊé•Â§±Ë¥•',
							icon: 'none',
							duration: 3000
						})
						return
					}
					
					// ÊµãËØïÊàêÂäü
					const sceneCount = data ? data.length : 0
					console.log('‚úÖ ËøûÊé•ÊàêÂäüÔºÅÊâæÂà∞', sceneCount, '‰∏™Âú∫ÊôØ')
					
					this.testResult = {
						type: 'success',
						message: `‚úÖ ËøûÊé•ÊàêÂäüÔºÅÊâæÂà∞ ${sceneCount} ‰∏™Âú∫ÊôØ`
					}
					
					uni.showToast({
						title: `ËøûÊé•ÊàêÂäüÔºåÊâæÂà∞ ${sceneCount} ‰∏™Âú∫ÊôØ`,
						icon: 'success',
						duration: 2000
					})
					
					// Â¶ÇÊûúÊúâÊï∞ÊçÆÔºåÂèØ‰ª•Êõ¥Êñ∞Êú¨Âú∞Âú∫ÊôØÂàóË°®
					if (data && data.length > 0) {
						console.log('Âú∫ÊôØÊï∞ÊçÆ:', data)
						// ËøôÈáåÂèØ‰ª•Â∞ÜÊï∞ÊçÆÂ∫ìÁöÑÂú∫ÊôØÊï∞ÊçÆÊõ¥Êñ∞Âà∞Êú¨Âú∞
						// this.loadScenesFromDatabase(data)
					}
					
				} catch (err) {
					console.error('ÊµãËØïÂºÇÂ∏∏:', err)
					this.testResult = {
						type: 'error',
						message: `‚ùå ÊµãËØïÂºÇÂ∏∏: ${err.message || 'Êú™Áü•ÈîôËØØ'}`
					}
					uni.showToast({
						title: 'ÊµãËØïÂºÇÂ∏∏',
						icon: 'none',
						duration: 3000
					})
				} finally {
					this.testing = false
				}
			},
			
			// ÊéíÂ∫èÈÄâÊã©‰∏ãÊãâÊ°ÜÂèòÂåñ
			onSortChange(e) {
				this.sortIndex = e.detail.value;
			},
			
			// ÂàÜÁ±ªÊ†áÁ≠æÁÇπÂáª
			onCategoryChange(category) {
				this.activeCategory = category;
			},
			
			// Âú∫ÊôØÈ°πÁÇπÂáª
			onSceneSelect(scene) {
				if (!scene || !scene.id) {
					uni.showToast({
						title: 'Âú∫ÊôØÊï∞ÊçÆÁº∫Â§±',
						icon: 'none'
					})
					return
				}
				uni.navigateTo({
					url: `/pages/dialog/dialog?id=${scene.id}`
				})
			}
		}
	}
</script>

<style>
	/* ÂÖ®Â±ÄÊ†∑ÂºèÈáçÁΩÆ */
	page {
		background-color: #f8f9fa;
		font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
	}
	
	.app-container {
		min-height: 100vh;
		padding: 20rpx;
	}
	
	.main-content {
		max-width: 600rpx;
		margin: 0 auto;
	}
	
	/* Âú∫ÊôØÈÄâÊã©ÈÉ®ÂàÜ */
	.scene-section {
		background-color: #fff;
		border-radius: 20rpx;
		padding: 30rpx;
		box-shadow: 0 4rpx 12rpx rgba(0, 0, 0, 0.05);
	}
	
	.section-title {
		font-size: 30rpx;
		color: #333;
		margin-bottom: 24rpx;
		display: block;
		line-height: 48rpx;
	}
	
	.emoji {
		margin-right: 10rpx;
	}
	
	/* Âú∫ÊôØÈÄâÊã©‰∏ãÊãâÊ°Ü */
	.scene-picker {
		background-color: #f5f5f5;
		border-radius: 10rpx;
		padding: 20rpx;
		margin-bottom: 20rpx;
	}
	
	.picker-text {
		font-size: 28rpx;
		color: #666;
	}
	
	/* ÂàÜÁ±ªÊ†áÁ≠æ */
	.category-tabs {
		display: flex;
		flex-wrap: wrap;
		gap: 15rpx;
		margin-bottom: 30rpx;
	}
	
	.category-tab {
		padding: 12rpx 20rpx;
		background-color: #f0f0f0;
		border-radius: 20rpx;
		font-size: 24rpx;
		color: #666;
		cursor: pointer;
		transition: all 0.3s;
	}
	
	.category-tab.active {
		background-color: #ff6b6b;
		color: #fff;
	}
	
	/* Âú∫ÊôØÂàóË°® */
	.scene-list {
		display: flex;
		flex-direction: column;
		gap: 20rpx;
	}
	
	.scene-item {
		background-color: #fafafa;
		border-radius: 15rpx;
		padding: 25rpx;
		border-left: 6rpx solid #ff6b6b;
		cursor: pointer;
		transition: all 0.3s;
	}
	
	.scene-item:hover {
		background-color: #f0f0f0;
		transform: translateY(-2rpx);
		box-shadow: 0 4rpx 12rpx rgba(0, 0, 0, 0.1);
	}
	
	.scene-title {
		font-size: 28rpx;
		color: #333;
		line-height: 42rpx;
		margin-bottom: 10rpx;
		display: block;
	}
	
	.scene-stats {
		font-size: 22rpx;
		color: #999;
	}

	/* Ê¨¢ËøéÊèèËø∞Âç°Áâá */
	.welcome-card {
		background-color: #f9fafb;
		border: 1px solid #eef2f7;
		border-radius: 16rpx;
		padding: 24rpx;
		margin-bottom: 20rpx;
	}
	.welcome-title {
		font-size: 32rpx;
		font-weight: 600;
		color: #222;
		display: block;
		margin-bottom: 12rpx;
	}
	.welcome-desc {
		font-size: 26rpx;
		color: #666;
		line-height: 40rpx;
		display: block;
	}
	
	/* ÊµãËØïÂå∫Âüü */
	.test-section {
		margin-bottom: 30rpx;
		padding: 20rpx;
		background-color: #f8f9fa;
		border-radius: 10rpx;
	}
	
	.test-btn {
		width: 100%;
		padding: 20rpx;
		background-color: #007aff;
		color: #fff;
		border-radius: 10rpx;
		font-size: 28rpx;
		border: none;
	}
	
	.test-btn:active {
		background-color: #0056b3;
	}
	
	.test-result {
		margin-top: 20rpx;
		padding: 15rpx;
		border-radius: 8rpx;
		font-size: 24rpx;
		line-height: 36rpx;
	}
	
	.test-result.success {
		background-color: #d4edda;
		color: #155724;
		border: 1px solid #c3e6cb;
	}
	
	.test-result.error {
		background-color: #f8d7da;
		color: #721c24;
		border: 1px solid #f5c6cb;
	}
</style>
